<template>
  <div class="valid">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="codeArea">
<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;form id="form" action="http://m.baidu.com/"&gt;
  用户名：&lt;input id="userName"&gt;&lt;br /&gt;
  &lt;button id="mySubmit"&gt;提交&lt;/button&gt;
&lt;/form&gt;
&lt;script type="text/javascript"&gt;
    import valid from "cy_static/valid";
    import ui from "cy_static/ui";
    valid.config({
      alertMeg : true,
      form : "#form",
      submitBtn : "#mySubmit"
    }); // 各项校验规则     
    valid("#userName").initValid({
      onShow : "请输入你的大号",
      onFocus : "用户名3个字哦！",
      oncorrect : "大号忘记输入了",
    }).inputValid({
      min : "5",
      max : "10",
      onError : "不长心啊，请输入5-10个字"
    });
&lt;/script&gt;
</xmp >

        </div>
        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/runcode"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">valid 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import valid from "cy_static/valid";
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;script type="text/javascript"&gt;
  import valid from "cy_static/valid";
  valid.config({
    alertMeg : true,
    form : "#form",
    submitBtn : "#mySubmit"
  }); // 各项校验规则     
  valid("#userName").initValid({}).inputValid({
    min : "5",
    max : "10",
    onError : "不长心啊，请输入5-10个字"
  });
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">实例方法</h2>
        <ul class="ul-props">					
					<li>
						<p><a>serialize</a>(form : Object|String = null) : <a>String</a></p>
						<p>输出序列化表单值的结果；</p>
					</li>
					<li>
						<p><a>serializeArray</a>(form : Object|String = null) : <a>Array</a></p>
						<p>输出以数组形式序列化表单值的结果；</p>
					</li>
					<li>
						<p><a>initValid</a>(params : Object = {}) : <a>object</a></p>
						<p>初始化组件获得|失去焦点默认值：必须项；</p>
					</li>
					<li>
						<p><a>inputValid</a>(params : Object = {}) : <a>object</a></p>
						<p>组件值<b>最大&amp;最小</b>范围校验 ；</p>
            <div class="showDetail">详细</div>
            <table class="table-props border">
              <tr>
                <th colspan="5">params 参数</th>
              </tr>
              <tr>
                <th style="width:20%">参数名</th>
                <th style="width:20%">类型</th>
                <th style="width:20%">默认值</th>
                <th style="width:30%">描述</th>
                <th style="width:10%" class="text-c">必需</th>
              </tr>
              <tr>
                <td>type</td>
                <td class="atn">String</td>
                <td>"size"</td>
                <td>(对select无效)<br> "size":表示比较长度； <br> "number":数值型比较；<br>
                  "string":字符型比较；<br> "date":短日期类型；<br> "datetime":长日期类型</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>min</td>
                <td class="atn">String</td>
                <td>0</td>
                <td>最小长度/值 0<br> 输入框比较的是关系；<br> 单|多选校验选项个数</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>max</td>
                <td class="atn">String</td>
                <td>99999999999999</td>
                <td>最大长度/值 <br> 输入框比较的是关系；<br> 单|多选校验选项个数</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onError</td>
                <td class="atn">String</td>
                <td>"输入错误"</td>
                <td>错误时的提示语</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onErrorMin</td>
                <td class="atn">String</td>
                <td>""</td>
                <td>比min属性小的提示</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onErrorMax</td>
                <td class="atn">String</td>
                <td>""</td>
                <td>比max属性大的提示</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>empty</td>
                <td>object</td>
                <td>{<br> leftEmpty: true,<br> rightEmpty: true,<br>
                  emptyError: null<br> };</td>
                <td>leftEmpty:左边是否允许为空；<br> rightEmpty:右边是否允许为空； <br>
                  emptyError：没有提示语时，显示onError提示语</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
								<td colspan="5">
<codemirror ref="demoCode1" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    valid.config({form : "#form"});
    valid("#name").initValid({}).inputValid({
      min : "5",
      max : "10",
      onError : "值范围不对",
      onErrorMin : "最小输入5个汉字",
      onErrorMax : "最小输入10个汉字"
    });
&lt;/script&gt;
</xmp >
                  </td>
								</tr>
            </table>
					</li>
					<li>
						<p><a>regType</a>(params : Object = {}) : <a>object</a></p>
						<p>组件值进行<b>正则</b>表达式校验  ；</p>
            <div class="showDetail">详细</div>
            <table class="table-props border">
              <tr>
                <th colspan="5">params 参数</th>
              </tr>
              <tr>
                <th style="width:20%">参数名</th>
                <th style="width:20%">类型</th>
                <th style="width:20%">默认值</th>
                <th style="width:30%">描述</th>
                <th style="width:10%" class="text-c">必需</th>
              </tr>
              <tr>
                <td>regExp</td>
                <td class="atn">String|Array</td>
                <td>""</td>
                <td>正则表达式或regexEnum对象的属性名</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>dataType</td>
                <td class="atn">String</td>
                <td>"string"</td>
                <td>"string":自定义的表达式；<br> "enum":regexEnum属性名</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>operateor</td>
                <td class="atn">String</td>
                <td>""</td>
                <td>多个表达式时的运算符类型；<br> "||"或的关系 "&amp;&amp;"并列</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onError</td>
                <td class="atn">String</td>
                <td>"输入错误"</td>
                <td>错误时的提示语</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
								<td colspan="5">
<codemirror ref="demoCode2" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal2" style="display:none">
&lt;script type="text/javascript"&gt;
    valid.config({form: "#form"});
    valid("#name").initValid({}).regType({
        regExp: /\d/,
        onError: "输入值需是数字",
    });

    valid("#name").initValid({}).regType({
        regExp: ["num","intege"],
        dataType : "enum",
        onError: "输入值需是数字且是整数"
    });
&lt;/script&gt;
</xmp >
                  </td>
								</tr>
            </table>
					</li>
					<li>
						<p><a>funType</a>(params : Object = {}) : <a>object</a></p>
						<p>组件值进行<b>函数</b>表达式校验  ；</p>
            <div class="showDetail">详细</div>
            <table class="table-props border">
              <tr>
                <th colspan="5">params 参数</th>
              </tr>
              <tr>
                <th style="width:20%">参数名</th>
                <th style="width:20%">类型</th>
                <th style="width:20%">默认值</th>
                <th style="width:30%">描述</th>
                <th style="width:10%" class="text-c">必需</th>
              </tr>
              <tr>
                <td>fun</td>
                <td class="atn">Function</td>
                <td>function(){}</td>
                <td>
                  外部函数名<br> 参数1:元素的值；<br> 
                  参数2:元素对象；<br>
                  返回string为自定义错误
                </td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>onError</td>
                <td class="atn">String</td>
                <td>"输入错误"</td>
                <td>函数返回false错误的提示;</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
								<td colspan="5">
<codemirror ref="demoCode3" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal3" style="display:none">
&lt;script type="text/javascript"&gt;
    valid.config({form: "#form"});
    valid("#name").initValid({}).funType({
      fun : function(val, dom) {// code
        return "自定义错误提示";
      },
      onError : "输入错误"
    });

    valid("#name").initValid({}).funType({
      fun : function(val, dom) {// code
        return false;
      },
      onError : "输入错误"
    });
&lt;/script&gt;
</xmp >
                  </td>
								</tr>
            </table>
					</li>
					<li>
						<p><a>ajaxType</a>(params : Object = {}) : <a>object</a></p>
						<p>组件值进行<b>ajax</b>异步校验 ；</p>
					</li>
					<li>
						<p><a>compareType</a>(params : Object = {}) : <a>object</a></p>
						<p>组件值进行<b>关系比较</b>校验；</p>
            <div class="showDetail">详细</div>
            <table class="table-props border">
              <tr>
                <th colspan="5">params 参数</th>
              </tr>
              <tr>
                <th style="width:20%">参数名</th>
                <th style="width:20%">类型</th>
                <th style="width:20%">默认值</th>
                <th style="width:30%">描述</th>
                <th style="width:10%" class="text-c">必需</th>
              </tr>
              <tr>
                <td>desID</td>
                <td class="atn">String|object</td>
                <td>""</td>
                <td>要跟源目标进行比较的目标组件（选择器|对象）</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>operateor</td>
                <td class="atn">String</td>
                <td>""</td>
                <td>比较符号：=、!=、&gt;、&gt;=、&lt;、&lt;=</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>dataType</td>
                <td class="atn">String</td>
                <td>"string"</td>
                <td>值："string"、"number","datetime","date"</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onError</td>
                <td class="atn">String</td>
                <td>"输入错误"</td>
                <td>错误时的提示语</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
								<td colspan="5">
<codemirror ref="demoCode4" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal4" style="display:none">
&lt;script type="text/javascript"&gt;
    valid.config({form: "#form"});
    valid("#name").initValid({}).compareType({
      desID : "#lastname",
      operateor : "=",
      onError : "两次名称不相等"
    });

    valid("#stime").initValid({}).compareType({
      desID : "#ntime",
      operateor : "&lt;",
      onError : "结束时间必需大于开始时间"
    });
&lt;/script&gt;
</xmp >
                  </td>
								</tr>
            </table>
					</li>
					<li>
						<p><a>unValid</a>(selector : String|object, valid : Object = {}) : <a>Void</a></p>
						<p>取消已绑定组件校验；selector选择器|对象；valid校验组实例，默认最新一组;</p>
					</li>
				</ul>
      </div>
      <div class="apiModule">
        <h2 class="cy_h2">静态方法</h2>
        <ul class="ul-props">
					<li>
						<p><a>config</a>(params : Object = {}) : <a>Object</a></p>
						<p>配置验证表单</p>
					</li>
					<li>
						<p><a>getValid</a>(num : Number = null) : <a>Object</a></p>
						<p>获取表单验证实例</p>
					</li>
					<li>
						<p><a>addValidItem</a>(dom : Object|String  = null) : <a>Void</a></p>
						<p>添加的验证元素；</p>
					</li>
					<li>
						<p><a>getValidItem</a>(num : Number = null) : <a>Object</a></p>
						<p>获取num||最后添加的验证元素；</p>
					</li>
					<li>
						<p><a>addValidItemRule</a>(rule : Object, dom : Object|String  = null) : <a>Void</a></p>
						<p>为验证项增加验证规则；dom为空时为最后的一项元素增加;</p>
					</li>
					<li>
						<p><a>clear</a>(params : Object = {}) : <a>Void</a></p>
						<p>清空所有表单验证</p>
					</li>					
				</ul>
      </div>



    </div> 
  </div>
</template>

<script>
import {showDetail,trim} from "../../../assets/js/codeStyle";
export default {
  name: 'valid',
  data () {
    return {
      msg: 'valid',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      },
      cmOptions2:{
            readOnly:"true",
            theme : "mdn-like",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
    showDetail();
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>  
<style scoped>
  .cy_example .codeArea{height:425px}
  .cy_example .iframeBox{flex:10}
</style>



